body {
    background: rgb(238, 201, 218);
    background: radial-gradient(circle, rgba(238, 201, 218, 1) 0%, rgba(158, 172, 189, 1) 84%);     margin: 0;
    padding: 30px;
}

.search-form {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 20vh;
    position: relative;
}

.search-form::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0.8;
    z-index: -1;
}

.search-form input[type="text"] {
    padding: 14px 30px;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    width: 300px;
    margin-right: 10px;
}

.search-form input[type="text"]:focus {
    border-color: #007bff;
    box-shadow: 0 1px 4px rgba(125, 101, 128, 1),
        -23px 0 20px -23px rgba(10, 30, 77, 1),
        23px 0 20px -23px rgba(10, 30, 77, 1),
        inset 0 0 40px rgba(0, 0, 0, 0.1);
    outline: none;
}

.search-form button[type="submit"] {
    padding: 14px 30px;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    background-color: rgba(10, 30, 77, 1);
    color: #fff;
    cursor: pointer;
}

.search-form button[type="submit"]:hover {
    background: linear-gradient(0deg, rgba(125, 101, 128, 1) 5%, rgba(10, 30, 77, 1) 95%);
    box-shadow: 0 1px 4px rgba(0, 0, 0, .3),
            -23px 0 20px -23px rgba(0, 0, 0, .6),
            23px 0 20px -23px rgba(0, 0, 0, .6),
            inset 0 0 40px rgba(0, 0, 0, .1);
}