body {
  background: radial-gradient(circle, #eec9da 0%, #9eacbd 84%);
  margin: 0;
  padding: 30px;
}

.search-form {
  justify-content: center;
  align-items: center;
  height: 20vh;
  display: flex;
  position: relative;
}

.search-form:before {
  content: "";
  opacity: .8;
  z-index: -1;
  position: absolute;
  inset: 0;
}

.search-form input[type="text"] {
  border: none;
  border-radius: 5px;
  width: 300px;
  margin-right: 10px;
  padding: 14px 30px;
  font-size: 16px;
}

.search-form input[type="text"]:focus {
  border-color: #007bff;
  outline: none;
  box-shadow: 0 1px 4px #7d6580, -23px 0 20px -23px #0a1e4d, 23px 0 20px -23px #0a1e4d, inset 0 0 40px #0000001a;
}

.search-form button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #0a1e4d;
  border: none;
  border-radius: 5px;
  padding: 14px 30px;
  font-size: 16px;
}

.search-form button[type="submit"]:hover {
  background: linear-gradient(0deg, #7d6580 5%, #0a1e4d 95%);
  box-shadow: 0 1px 4px #0000004d, -23px 0 20px -23px #0009, 23px 0 20px -23px #0009, inset 0 0 40px #0000001a;
}

